@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  --bubble-gum-pink: #ff5ab7;
  --black: #1e1e1e;
  --pinkish-grey: #c4c4c4;
  --warm-grey: #888;
  --black-two: #121212;
  --white: #fff;
  --dodger-blue: #379cf6;
  --medium-blue: #307bbe;
  --black-5: rgba(0, 0, 0, 0.05);
  --azure: #0290ff;
  --white-two: #fafafa;
  --pale-grey: #f5f6fa;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #fafafa;
  background-color: var(--white-two);
}

a {
  text-decoration: none;
  color: inherit;
}

html {
  font-size: 100%;
  background-color: #fafafa;
  background-color: var(--white-two);
}

body {
  font-family: 'Montserrat', sans-serif;
}

.container {
  width: 80%;
  margin: auto;
}
.addBook {
  display: flex;
  align-items: stretch;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin: auto;
  width: 100%;
}

h4 {
  font-size: 1.25rem;
  font-weight: bold;
  letter-spacing: -0.18px;
  color: var(--warm-grey);
  margin-bottom: 1.2rem;
  flex: 0 0 100%;
}

form {
  width: 100%;
  display: flex;
}

form input {
  height: 1.8rem;
  margin-right: 2.5rem;
}

.submit-input {
  width:  13%;
  border: none;
  cursor: pointer;
  border-radius: 3px;
  background-color: var(--azure);
  font-size: 0.813rem;
  font-weight: bold;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
}

.input {
  width: 30%;
  padding: 0.8rem 1rem;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #fff;
  letter-spacing: -0.15px;
  opacity: 0.6;
  color: var(--black-two);
}

.book-container {
  padding: 2rem 9.188rem 1.75rem 1.688rem;
  border-radius: 0.4rem;
  border: solid 1px #e8e8e8;
  background-color: var(--white);
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  max-height: 15rem;
  margin-bottom: 1.2rem;
}

.book-info {
  flex-grow: 0.5;
  display: flex;
  flex-direction: column;
}

.book-info h1 {
  margin: 0.188rem 0 0;
  font-size: 1.375rem;
  font-weight: bold;
  letter-spacing: -0.2px;
  color: var(--black-two);
}

.book-info p {
  margin: 0;
  margin-bottom: 0.25rem;
  opacity: 0.5;
  font-size: 0.875rem;
  font-weight: bold;
  color: var(--black-two);
}

.book-info button {
  margin: 0.188rem 0.938rem 0.125rem 0;
  padding-right: 1rem;
  font-size: 0.875rem;
  font-weight: 300;
  color: #4386bf;
  border: none;
  cursor: pointer;
  background-color:transparent;
  font-family: 'Roboto Slab', serif;
  border-right: solid 1px #e8e8e8;
}

.book-chart {
  display: flex;
}

.progress {
  height: 4.25rem;
  width: 4.25rem;
}

.percentage p {
  height: 2.438rem;
  margin: auto 1.2rem;
  font-family: 'Montserrat', serif;
  font-size: 2rem;
  color: #121212;
}

h3 {
  height: 1.125rem;
  margin: auto 1.2rem;
  opacity: 0.5;
  font-family: 'RobotoSlab', serif;
  font-size: 0.813rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

.book-progress h3 {
  opacity: 0.5;
  font-size: 0.813rem;
  font-weight: 300;
  color: var(--black-two);
  margin-bottom: 0.4rem;
}

.book-progress p {
  font-size: 1rem;
  font-weight: 300;
  letter-spacing: -0.4px;
  color: var(--black-two);
  margin-bottom: 1.4rem;
}

.book-progress button {
  border: none;
  padding: 0.5rem 1.375rem;
  border-radius: 3px;
  background-color: var(--azure);
  font-size: 0.813rem;
  font-weight: 300;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
}

.book-collection {
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 3rem;
  max-width: 80vw;
  margin-top: 7.5rem;
  display: flex;
  flex-direction: column;
}

header {
  background-color: var(--white);
  width: 100%;
  border-bottom: solid 0.15rem #e8e8e8;
  display: flex;
  height: 6.5rem;
  padding: 1.438rem 6.25rem 1.688rem;
  top: 0;
  position: fixed;
  justify-content: space-between;
  align-items: center;
}

nav {
  display: flex;
  align-items: center;
}

header h1 {
  height: 2.313rem;
  margin: 0.375rem 2.938rem 0.125rem 0;
  font-family: 'Montserrat', serif;
  font-size: 1.875rem;
  font-weight: bold;
  color: var(--azure);
}

header li {
  display: inline-block;
  margin: auto 0.5rem;
  font-size: 1.5rem;
  color: #121212;
}

header a {
  color: var(--black-two);
  width: 3.688rem;
  height: 1rem;
  margin: 1.125rem 2.563rem 0.688rem 0;
  font-family: 'Montserrat', serif;
  font-size: 0.813rem;
  letter-spacing: 0.19rem;
  text-decoration: none;
  opacity: 0.5;
}

img {
  width: 2rem;
}

